import Validator from 'validator'
import isEmpty from 'lodash/isEmpty'

export default (model, key = '') => {
  const errors = {}

  if (!Validator.isEmail(model.email)) {
    errors.email = '–ù–µ –≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –ø–æ—á—Ç—ã'
  }
  if (!Validator.isLength(model.password, { min: 6, max: undefined })) {
    errors.passwordLength = '–î–ª–∏–Ω–∞ –ø–∞—Ä–æ–ª—è –Ω–µ –º–µ–Ω–µ–µ 6 —Å–∏–º–≤–æ–ª–æ–≤'
  }
  if (isEmpty(model.password)) {
    errors.passwordEmpty = '–ü–æ–ª–µ –ø–∞—Ä–æ–ª—å –Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'
  }
  if (key === 'reg') {
    if (isEmpty(model.repassword)) {
      errors.repassword = '–ü–æ–ª–µ re-password –Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'
    }
    if (model.password !== model.repassword) {
      errors.different = '–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç'
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}



